import React from "react";
import styled from "astroturf";

import { Button } from "./Buttons";

export const IconButton = styled(Button, null, "IconButton", require("./IconButtons-IconButton.module.scss"), "icon-button", "iconButton");

export const Hamburger = styled(IconButton, null, "Hamburger", require("./IconButtons-Hamburger.module.scss"), "hamburger", "hamburger");

export const IconBtn = ({ children, ...props }) => {
  return <Button {...props}>{children}</Button>;
};
